.footer {
  padding: 30px 20px 40px;
  background: #8c9772;

  &__grid {

    @include onTablet {
      display: grid;
      grid-template-columns: repeat(6, 1fr);
      column-gap: 20px;

    }

    @include onDesktop {
      display: grid;
      grid-template-columns: repeat(12, 1fr);
      column-gap: 20px;
    }
  }

  &__contact {
    display: flex;
    flex-direction: column;

    // :nth-child(-n + 2) {
    //   margin-bottom: 11px;
    // }

    @include onDesktop {
      grid-column: 1/3;
      flex-direction: row;

    }

    &--hover {
      margin-bottom: 15px;
      @include onTablet {
        grid-column: 1;
        font-size: 14px;

        // :nth-child(-n + 2) {
        //   margin-bottom: 15px;
        // }
      }

      @include onDesktop {
        margin-right: 40px;
      }

      &:hover {
        // transition: transform 0.3s;
        // &:hover {
        //   color: #e4dfd3;
        //   transform: scale(1.1);
        // }
        position: relative;
        width: max-content;
        &::after {
          position: absolute;
          content: "";
          display: block;
          width: 100%;
          height: 1px;
          background: #e4dfd3;
          margin-top: 1px;
        }
      }
    }
    &--bot {
      margin-bottom: 20px;
    }

    &--font {

      width: max-content;
      text-decoration: none;
      font-weight: 500;
      font-size: 18px;
      line-height: 150%;

      text-transform: capitalize;
      color: #e4dfd3;
      @include onDesktop {
        grid-column: 1 / 4;
        display: flex;
        justify-content: space-between;
        font-size: 18px;
      }
    }
  }

  &__inet {
    margin-bottom: 40px;

    @include onTablet {
      grid-column: 1;
      margin-bottom: 50px;
    }

    @include onDesktop {
      margin: 0;
      grid-column: 9;
    }

    &-in {
      padding-left: 26px;
    }

    &-none {
      text-decoration: none;
    }

  }

  &__info {
    margin-bottom: 40px;
    @include onTablet {
      grid-column: 4 / 7;
      grid-row: 1;
      margin: 0;
    }

    @include onDesktop {
      grid-column: 9 / 13;
    }

    &-font {
      margin-bottom: 10px;
      font-weight: 400;
      font-size: 14px;
      line-height: 150%;
      color: #e4dfd3;
      text-decoration: none;

      @include onTablet {
        font-size: 14px;
        :nth-child(-n+3) {
          margin-bottom: 7px;
        }
      }

      @include onDesktop {
        font-size: 16px;

        &:last-child {
          margin-bottom: 30px;
        }

      }
    }
  }

  &__end {
    font-weight: 600;
    font-size: 14px;
    line-height: 130%;
    text-align: center;
    color: #e4dfd3;

    @include onTablet {
      grid-column: 1 / 3;
      text-align: start;
    }

    @include onDesktop {
      grid-column: 1 / 5;
      grid-row: 2;
      text-align: start;

    }

    &-photo {
      width: 10px;
      height: 10px;
    }

    @include onDesktop {
      font-size: 16px;
    }
  }

}
